import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import logo from '../logof.png'


const Navbar = () => {

    const [shownav, setShowNav] = useState("false");
    const[navBg,setNavBg]=useState(false);


    const ChangeBg =()=>{
        console.log(window.scrollY);
        if ((window.scrollY) >=80){
            setNavBg(true);
        }
        else{
            setNavBg(false);
        }
    }
    window.addEventListener('scroll',ChangeBg);

{console.log(navBg)}
  return (
      <>
          <div className={navBg ? 'nav  flex bd-style nav-bg ' : 'flex bd-style nav ' }>
              <Link to='/' className='header-logo '>
                    <img src={logo} alt="" />
              </Link>
              <div className='letter-spacing-1 ff-serif header-heading' >
                  <h2>CryptLand</h2>
              </div>

              <button className={shownav ? "mobile-nav-hamicon" : "mobile-nav-close"} aria-controls="primary-navigation" onClick={() => setShowNav(!shownav)}><span className="sr-only" aria-expanded="false">Menu</span></button>


              <div className=' flex pmr '>
               <div>

               </div>
                  <div className=''>

                
                      <ul  className={shownav ? "  primary-nav  flex slide-out" : " primary-nav  flex slide-in"}>
                      <li>
                              <Link to="/nft" className='nav-links letter-spacing-1 '>NFT</Link>
                      </li>
                     
                  
                       <li>            
                              <Link to="/exchanges" className='nav-links letter-spacing-1 ' >Exchanges</Link>
                      </li>
                 
                 <li>
                              <Link to="/cryptcurrencies" className='nav-links letter-spacing-2 '>Cryptocurrencies</Link>
                 </li>
                      
                  
               
                  </ul> 
                  </div>
              </div>
          </div>
      
      
      
      
      </>
   
  )
}

export default Navbar